/*Animacion de carga*/
.carga{
    opacity: 0;
    
}
.carga-animate { 
    opacity: 1; 
    animation: --a 1s linear;
}
@keyframes --a {
    from{
opacity: 0;
    }
    to{
opacity: 1;
    }
    
}
/*Final*/
.prp{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.prp>ol>li{
cursor: pointer;
color: black;
transition: color 0.5s;
}
.prp>ol>li:hover{
cursor: pointer;
color: aquamarine;
}

.mapa{
    height: 60%;
    width: 60%;
}